.call-to-action-btn {
  color: #fff;
  background-color: #ccc;
  border-color: #eee;
  padding: 15px;
  border-radius: 2px;
  font-size: 18px;
  line-height: normal;
  -webkit-transition: all .5s ease-in;
  -moz-transition: all .5s ease-in;
  -o-transition: all .5s ease-in;
  transition: all .5s ease-in
}

