@media (max-width:992px) {
  nav a {
    font-size: 12px
  }
}

@media (max-width:767px) {

  .img-responsive,
  .round-content {
    margin-bottom: 1em
  }

  nav a {
    font-size: 18px
  }

  header .middle {
    position: inherit;
    left: inherit;
    top: inherit;
    transform: inherit
  }

  .top {
    display: none
  }

  .navbar-collapse li,
  .phone-resp {
    display: block
  }

  header.slider {
    padding: 0 0 70px;
    height: auto
  }

  header .middle h1 {
    font-size: 40px
  }

  .button-phone {
    font-size: 18px
  }

  .navbar-toggle {
    background-color: #238bfa
  }

  .navbar-collapse.in,
  .navbar-toggle .icon-bar {
    background-color: #fff
  }

  .navbar-collapse.in {
    border-top: 1px solid #09739f;
    position: relative;
    z-index: 99
  }

  nav a.active,
  nav a:hover {
    border-bottom: none
  }

  nav li:not(:first-child):after {
    background-color: transparent
  }

  .img-responsive {
    margin-top: 2em
  }

  .opinion .col-sm-4 {
    margin-bottom: 2em
  }

  footer {
    text-align: center
  }

  .phone-resp {
    padding: 15px 0
  }
}

.w30 {
