  }
}

.equipe {
  display: grid;
  grid-template-columns: repeat(3, 25vw);
  grid-column-gap: 5%;
  margin: 5% 0;
  justify-content: center;
}

.equipe .center_equipe {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.equipe img {
  border-radius: 100%;
  width: 15vw;
}

.equipe p {
  font-size: 15px;
  color: #000;
  margin-bottom: 15%
}

@media only screen and (max-width: 769px) {
  .equipe {
    display: flex;
    flex-direction: column;
  }

  .equipe img {
    width: 50vw;
  }
}

.avis_plus {
  display: flex;
  justify-content: center;
  margin: 5% 0;
  flex-wrap: wrap;
}

.avis_plus img {
  height: 20vh;
  margin: 0 5vw;
}

.br_none {
  border-radius: inherit !important;
}

.gal {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem;
}

.card {
  width: 20vw;
  height: 20vw;
  perspective: 500px;
  margin: 0 auto;
}

.content {
  position: absolute;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  transition: transform 1s;
  transform-style: preserve-3d;
}

.card:hover .content {
  transform: rotateY(180deg);
  transition: transform 0.5s;
}

.front,
.back {
  position: absolute;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 300px;
  color: #03446A;
  text-align: center;
  font-size: 60px;
  border-radius: 5px;
  backface-visibility: hidden;
}

.front img,
.front {
  object-fit: cover;
  height: 100%;
  width: 100%;
  border-radius: 15px;
}

.back {
  background: #fff;
  color: white;
  transform: rotateY(180deg);
}

.back p {
  color: #000;
}

@media (max-width: 767px) {
  .gal {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .card {
    width: 90vw;
    height: 50vh;
    margin: 5% 0;
  }
}

.round-img {
  border-radius: 100%;
  height: 10vw !important;
  width: 10vw !important;
  object-fit: cover;
}

.round_slider {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.round_slider p {
  margin-top: 5%;
}

.w-full {
  width: 100%
}




